<template>
  <div class="card-container" ref="container">
    <van-card
        v-for="it in list"
        :key="it.id"
        :price="it.price"
        :desc="it.desc"
        :title="it.name"
        :thumb="it.coverImg"
    />
  </div>
</template>

<script>
  import {getList} from "@/api/cart";

  export default {
   data() {
     return {
       list: [],
       page: 1,
       limit: 10
     }
   },
    async created() {
     const resp = await getList(this.limit,this.page);
      this.list = resp.products;
    },
    methods: {
      async handleScroll() {
        const range = 900;
        const scroll = this.$refs.container.scrollHeight - this.$refs.container.scrollTop;
        if(scroll <= range) {
          this.page++;
          const resp = await getList(this.limit,this.page);
          this.list = this.list.concat(resp.products);
        }
      }
    },
    mounted() {
      console.log(this.$refs.container)
     this.$refs.container.addEventListener('scroll',this.handleScroll);
    },
    beforeDestroy() {
      this.$refs.container.removeEventListener('scroll',this.handleScroll);
    }
  }

</script>

<style scoped>
.card-container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
</style>