<template>
  <div class="rotation-container" ref="container" @mouseover="stop" @mouseleave="autoPlay">
    <div class="carousel">
      <ul class="carousel-img"  :style="{marginLeft,width}">
        <li v-for="it in list" :key="it.id">
          <CarouselImg :url="it.coverImg"/>
        </li>
      </ul>
      <div class="btn leftBtn" @click="changeIndex(index - 1)">&lt;</div>
      <div class="btn rightBtn" @click="changeIndex(index + 1)">&gt;</div>
      <ul class="dots">
        <li v-for="(it,i) in list" :key="it.id" :class="{active: index === i}" @click="changeIndex(i)"></li>
      </ul>
    </div>
    <ul class="products">
      <li v-for="it in list" :key="it.id">
        <div>
          <img :src="it.coverImg" alt="">
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  import CarouselImg from "./components/CarouselImg";
  import {setPrice} from "@/utils/setPrice";
  import {getList} from "@/api/cart";

  export default {
    data() {
      return {
        list:[],
        index: 0,
        containerWidth: 0,
        timerId: null,
      }
    },
    components: {
      CarouselImg,
    },
    computed: {
      marginLeft() {
        return -this.index * this.containerWidth + 'px';
      },
      width() {
        return this.list.length * this.containerWidth + 'px';
      }
    },
    methods: {
      setPrice,
      changeIndex(i) {
        if(i < 0) {
          i = this.list.length - 1;
        }
        if(i >= this.list.length) {
          i = 0;
        }
        this.index = i;
      },
      autoPlay() {
        this.timerId = setInterval(() => {
          this.changeIndex(this.index + 1)
        },1000)
      },
      stop() {
        clearInterval(this.timerId);
        this.timerId = null;
      },
    },
    async created() {
      const resp = await getList();
      this.list = resp.products;
      console.log(this.list)
    },
    mounted() {
      this.containerWidth = this.$refs.container.clientWidth;
      this.autoPlay();
    },
    destroyed() {
      this.stop();
    }
  }
</script>

<style scoped lang="less">
.rotation-container {
  width: 100%;
  height: 100%;
  position: relative;
  .carousel {
    width: 100%;
    height: 400px;
    position: relative;
  }
  .carousel-img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: .3s;
    li {
      width: 100%;
      height: 100%;
      float: left;
    }
  }
  .btn {
   position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #fff;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    background-color: rgba(0,0,0,.5);
    cursor: pointer;
  }
  .leftBtn {
    left: 0;
  }
  .rightBtn {
    right: 0;
  }
  .dots {
    width: 80%;
    height: 20px;
    position: absolute;
    bottom: 80px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-around;
    background-color: rgba(0,0,0,.5);
    align-items: center;
    li {
      width: 10px;
      height: 10px;
      background-color: #fff;
      border-radius: 50%;
      &.active {
        background-color: black;
      }
    }
  }
  .products {
    width: 100%;
    li {
      width: 100%;
      height: 200px;
      margin: 20px 0;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
</style>